
#login{
  height:100vh;
  width: 100%;
  background-color:#2c2c2c;

}
#register{
  height:100vh;
  width: 100%;
  background-color:#2c2c2c;
}
#section{
  position: absolute;
  top: 50%;
  left: 50%;
  transform:translate(-50%,-50%);

}
#section .head h2{
  text-align: center;
  font-size: 40px;
  font-family: 'Rubik', sans-serif;
  color: #2fd498;
}
#section .textbox{
  width: 100%;
  margin:28px 0px;
  padding:8px 0px;
  border-bottom: 2px solid #2fd498;

}
#section .textbox input{
  background-color: transparent;
  border: none;
  font-size: 20px;
  color:white;
  outline:none;
}
#section .textbox i{
  color: white;
  font-size: 20px;
  padding: 0px 10px;
}
#section .log_btn button{
  background-color: transparent;
  border: 2px solid #2fd498;
  padding: 8px 20px;
  border-radius: 4px;
  color:white;
  outline: none;
  width: 100%;
  margin-bottom: 20px;
}
#section .log_btn button:hover{
  background-color:#2fd498 ;
  transition: 0.5s;
}
#section .reg_btn button{
  background-color: #2fd498;
  border: 2px solid #2fd498;
  width: 100%;
  padding: 8px 20px;
  border-radius: 4px;
  color:white;
  outline: none;
}
#section .reg_btn button:hover{
  background-color: transparent;
  transition: 0.5s;  
}
